import React, { Component } from 'react';

import { hexStringToString } from '../ggv/js/gegeUtil'

class Dashboard extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    let hash = window.location.hash;
    console.log(hash)
    if (hash) {
      let idx = hash.indexOf("#", 1);
      if (idx > 0) {
        let configHex = hash.substr(idx + 1);
        //console.log(query);
        let config = hexStringToString(configHex);
        let splits = config.split("#");
        if (splits.length > 0) {
          let trackers = splits[0];
          let miners = splits[1];
          let iceServers = splits[2];
          let trs = trackers.split(";");
          let mins = miners.split(";");
          window.localStorage.setItem("ctracker", trs[0]);
          window.localStorage.setItem("cminer", mins[0]);
          if(iceServers){
            window.localStorage.setItem("iServers", iceServers);
          }
        }
      }
    }
  }

  render() {
    return (
      <div style={{ height: '680px' }}>
        <iframe width='100%' height='100%' frameborder='0' name="_blank" src="/guide/index.html" />
      </div>
    );
  }
}

export default Dashboard;
